<template>
  <div id="app">
    <div 
      class="panel top"
      v-on:click="formCounterMinus"
      v-if="this.formCounter > 1"
    ></div>
    <div 
      class="panel top top__disabled"
      v-else-if="this.formCounter === 1"
    ></div>
    <form 
      @submit.prevent="createClient"
      class="animationDefault"
      :class="{
      animationFirst:this.formCounter === 1, 
      animationTwo:this.formCounter === 2,
      animationThree:this.formCounter === 3
      }"
    >
      <div class="personal-information">
        <div class="personal-information__container">
          <div class="personal-information__block">
            <p>Заполните поля ниже</p>
            <input
              type="text"
              placeholder="Фамилия"
              v-model="familyName"
              :class="{invalid: ($v.familyName.$dirty && !$v.familyName.required)}"
            >
            <small
              v-if="$v.familyName.$dirty && !$v.familyName.required"
            >Обязательное поле</small>
            <input
              type="text"
              placeholder="Имя"
              v-model="name"
              :class="{invalid: ($v.name.$dirty && !$v.name.required)}"
            >
            <small
              v-if="$v.name.$dirty && !$v.name.required"
            >Обязательное поле</small>
            <input type="text" placeholder="Отчество">
            <input
              type="text"
              placeholder="Дата рождения"
              v-model="birthday"
              :class="{invalid: ($v.birthday.$dirty && !$v.birthday.required) || ($v.birthday.$dirty && !$v.birthday.validateDate)}"
            >
            <small
              v-if="$v.birthday.$dirty && !$v.birthday.required"
            >Обязательное поле</small>
            <small
              v-else-if="$v.birthday.$dirty && !$v.birthday.validateDate"
            >Введите дату ДД.ММ.ГГГГ</small>
            <input 
              type="number" 
              placeholder="Номер телефона" 
              v-model.trim="phoneNumber"
              :class="{invalid: ($v.phoneNumber.$dirty && !$v.phoneNumber.required) || ($v.phoneNumber.$dirty && !$v.phoneNumber.minLength) || ($v.phoneNumber.$dirty && !$v.phoneNumber.maxLength) || ($v.phoneNumber.$dirty && !$v.phoneNumber.phoneNumberOne)}"
            >
            <small
              v-if="$v.phoneNumber.$dirty && !$v.phoneNumber.required"
            >Обязательное поле</small>
            <small
              v-else-if="$v.phoneNumber.$dirty && !$v.phoneNumber.phoneNumberOne"
            >Номер начинается с 7</small>
            <small
              v-else-if="$v.phoneNumber.$dirty && !$v.phoneNumber.minLength"
            >Минимальная длина номера {{$v.phoneNumber.$params.minLength.min}} символов</small>
            <small
              v-else-if="$v.phoneNumber.$dirty && !$v.phoneNumber.maxLength"
            >Максимальная длина номера {{$v.phoneNumber.$params.maxLength.max}} символов</small>
            <input type="text" placeholder="Пол">
          </div>
          <div class="personal-information__block">
            <select 
              multiple
              v-model="groupClients"
              :class="{invalid: ($v.groupClients.$dirty && !$v.groupClients.required)}"
            >
              <option disabled>Группа клиентов:</option>
              <option>VIP</option>
              <option>Проблемные</option>
              <option>ОMС</option>
            </select>
            <small
              v-if="$v.groupClients.$dirty && !$v.groupClients.required"
            >Выберите группу клиентов</small>
            <select size="4">
              <option disabled selected>Лечащий врач:</option>
              <option>Иванов</option>
              <option>Захаров</option>
              <option>Чернышева</option>
            </select>
            <p>Не отправлять смс <input type="checkbox"></p>
          </div>
        </div>
      </div>
      <div class="address">
        <div class="address__container">
          <p>Адрес</p>
          <input type="text" placeholder="Индекс">
          <input type="text" placeholder="Страна">
          <input type="text" placeholder="Область">
          <input
            type="text"
            placeholder="Город"
            v-model="City"
            :class="{invalid: ($v.City.$dirty && !$v.City.required)}"
          >
          <small
            v-if="$v.City.$dirty && !$v.City.required"
          >Обязательное поле</small>
          <input type="text" placeholder="Улица">
          <input type="text" placeholder="Дом">
        </div>
      </div>
      <div class="passport-data">
        <div class="passport-data__container">
          <p>Документы</p>
          <select
            size="4"
            v-model="typeDocument"
            :class="{invalid: ($v.typeDocument.$dirty && !$v.typeDocument.required)}"
          >
            <option disabled>Документы:</option>
            <option>Паспорт</option>
            <option>Свидетельство о рождении</option>
            <option>Вод. удостоверение</option>
          </select>
          <small
            v-if="$v.typeDocument.$dirty && !$v.typeDocument.required"
          >Выберите тип документа</small>
          <input type="number" placeholder="Серия">
          <input type="number" placeholder="Номер">
          <input type="text" placeholder="Кем выдан">
          <input
            type="text"
            placeholder="Дата выдачи"
            v-model="dateIssue"
            :class="{invalid: ($v.dateIssue.$dirty && !$v.dateIssue.required) || ($v.dateIssue.$dirty && !$v.dateIssue.validateDate)}"
          >
          <small
            v-if="$v.dateIssue.$dirty && !$v.dateIssue.required"
          >Обязательное поле</small>
          <small
            v-else-if="$v.dateIssue.$dirty && !$v.dateIssue.validateDate"
          >Введите дату ДД.ММ.ГГГГ</small>
          <small
            class="createCli"
            v-if="this.noErrors"
          >Новый клиент успешно создан</small>
          <button type="submit">Отправить</button>
        </div>
      </div>
    </form>
    <div
      class="panel bottom"
      v-on:click="formCounterPlus"
      v-if="this.formCounter < 3"
    ></div>
    <div
      class="panel bottom bottom__disabled"
      v-if="this.formCounter === 3"
    ></div>
  </div>
</template>

<script>
import {required, minLength, maxLength} from 'vuelidate/lib/validators'
import { phoneNumberOne, validateDate } from "./validators";
export default {
  name: 'App',
  data:()=>({
    familyName:'',
    name:'',
    birthday:'',
    phoneNumber:'',
    groupClients:[],
    City:'',
    typeDocument:[],
    dateIssue:'',
    formCounter:1,
    noErrors:false
  }),
  validations:{
    familyName:{
      required
    },
    name:{
      required
    },
    birthday:{
      required,
      validateDate
    },
    phoneNumber:{
      required,
      phoneNumberOne,
      minLength:minLength(11), 
      maxLength:maxLength(11),
    },
    groupClients:{
      required
    },
    City:{
      required
    },
    typeDocument:{
      required
    },
    dateIssue:{
      required,
      validateDate
    }
  },
  methods:{
    createClient(){
      if(this.$v.$invalid){
        this.$v.$touch()
      }
      if(this.$v.$error === false){
        this.noErrors = true
        console.log('Ошибок нету', this.noErrors)
      }else{
        this.noErrors = false
        console.log('Имеются ошибки', this.noErrors)
      }
      return
    },
    formCounterPlus(){
      if(this.formCounter != 3){
        this.formCounter++
      }
    },
    formCounterMinus(){
      if(this.formCounter != 1){
        this.formCounter--
      }
    }
  }
}
</script>

